<template>
  <div class="container active">
    <header>
      <div class="nav__logo active">
        <router-link class="badge" to="/" @click="clearSearch">
          <svg xmlns="http://www.w3.org/2000/svg" viewBox="156.58698734144505 187.07317629161378 36.8652687549646 37.98370116420466">
            <path d="M183.94 192.71C182.78 190.79 180.41 188.39 174.5 188.09C168.59 187.78 160.52 193 158.42 200.51C156.33 208.03 158 215.67 165.47 220.1C172.94 224.52 185.64 220.88 189.6 214.9C193.56 208.93 182.2 206.45 185.7 208.31C189.2 210.18 180.74 215.21 173.72 213.9C166.7 212.59 164.28 207.63 164.4 202.35C164.51 197.08 168.8 191.67 175.38 190.99C179.76 190.54 182.97 191.83 185 194.88C185.07 194.71 184.72 193.99 183.94 192.71Z"></path>
            <path d="M187.13 201.9C187.13 206.92 183.05 211 178.03 211C173 211 168.93 206.92 168.93 201.9C168.93 196.88 173 192.8 178.03 192.8C183.05 192.8 187.13 196.88 187.13 201.9Z"></path>
          </svg>
        </router-link>
        <router-link to="/" @click="clearSearch">
          <h1>Elderlake</h1>
        </router-link>
      </div>

      <div class="search">
        <input class="omnibox" type="text" placeholder="What would you like to know more about?" v-model="query" @keydown="submit">
        <button class="button" @click="search">Enter</button>
      </div>
    </header>

    <nav class="links">
      <ul>
        <li v-for="t in types" :key="t" :class="{ active: t === type }" @click="type = t">{{ t }}</li>
      </ul>
    </nav>

    <main>
      <transition-group
        name="list"
        tag="div"
      >
        <div v-for="(res, ind) in results" :key="ind" class="result list-item">
          <div class="result__image">
            <img :src="res.image" alt="">
          </div>
          <div class="result__details">
            <h2><a :href="res.link">{{ res.title }}</a></h2>
            <p v-content="res.content"></p>
          </div>
        </div>
      </transition-group>
    </main>
  </div>
</template>

<script>
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'

export default {
  name: 'Search',
  data() {
    return {
      query: "",
      searched: false,
      type: "all",
      types: ["all", "ELI5", "games", "news", "images"],
      results: [],
      queries: [
        {
          keywords: ['abstract reasoning', 'abstract', 'reasoning', 'eli5'],
          title: 'Abstract Reasoning',
          content: `<span class="quote">If I throw a rock at a window, the window will break.</span> is a precise statement that can only be applied to a specific scenario. <span class="quote">If I throw a hard and heavy object at an inflexible and weak object, the weak object will break.</span> is a similar statement, but more abstract, and can be applied to more than one scenario. Ability to think in abstract terms (Abstract Reasoning) allows you to relate past experiences to new experiences and have a better understanding of how things generally work.`,
        },
        {
          keywords: ['everett'],
          title: 'Everett',
          content: '<h3>What does it mean?</h3><p>Wild Boar, Strong</p><h3>Origin</h3><p>Anglo-Saxon</p>',
        },
        {
          keywords: ['sabastian', 'sebastian'],
          title: 'Sebastian',
          content: `<h3>What does it mean?</h3><p>From the Latin for <span class="quote">venerable</span> or <span class="quote">revered.</span></p><h3>Origin</h3><p>Greek</p>`,
        },
        {
          keywords: ['sierra'],
          title: 'Sierra',
          content: `<h3>What does it mean?</h3><p>Saw-tooth mountain range.</p><h3>Origin</h3><p>Spanish</p>`,
        }
      ],
    };
  },
  mounted() {
    if (this.$route.query && this.$route.query.query) {
      this.query = this.$route.query.query;
      this.search();
    }
  },
  directives: {
    content (el, { value }) {
      if (el.innerHTML !== value) {
        el.innerHTML = value
      }
    }
  },
  methods: {
    submit(event) {
      if (event.key === "Enter") {
        this.$router.push(`/search?query=${this.query}`);
        this.search();
      }
    },
    clearSearch() {
      this.$router.push("/");
    },
    search() {
      while (this.results.length > 0) {
        this.results.pop();
      }

      setTimeout(() => {
        // console.log(`Query: ${this.query}`);

        if (this.query && this.query.includes("news about")) {
          var data = null;

          var xhr = new XMLHttpRequest();
          xhr.withCredentials = true;

          let self = this;

          xhr.addEventListener("readystatechange", function () {
            if (this.readyState === 4) {
              let results = JSON.parse(this.responseText).articles;

              results.forEach((result) => {
                // console.log(result);
                let newResult = {
                  title: result.title,
                  content: result.description,
                  link: result.url,
                  image: result.urlToImage,
                };

                self.results.push(newResult);
              });

              self.searched = true;
            }
          });

          // xhr.open("GET", `http://newsapi.org/v2/top-headlines?country=us&category=${this.query.split("news about ")[1]}&apiKey=97813f407b8946c483f3b10dc0643c90`);
          
          xhr.open("GET", `http://newsapi.org/v2/everything?q=${this.query.split("news about ")[1]}&from=2021-01-18&sortBy=popularity&apiKey=97813f407b8946c483f3b10dc0643c90`);

          xhr.send(data);
        } else {
          for (var i = 0; i < this.queries.length; i++) {
            if (this.query && this.queries[i].keywords.includes(this.query.toLowerCase())) {
              // console.info("Query returned result!");
              // console.info(this.queries[i]);

              this.results.push({
                title: this.queries[i].title,
                content: this.queries[i].content,
                link: "",
                image: "",
              });

              this.searched = true;
            } else {
              // console.warn("Query did not yield any results, yet.");
              // this.result = "Not Found";
            }

            // console.log("Searching is complete.");
          }

        }
      }, (this.searched) ? 320 : 0);
    },
  },
}
</script>

